import {
  IonButton,
  IonContent,
  IonIcon,
  IonRow,
  IonGrid,
  IonItem,
  IonList,
  IonLabel,
  IonInput,
  IonCol,
  IonText
} from '@ionic/react';

import React, { useState, useRef } from 'react';

import {
  AuthenticationDetails,
  CognitoUserPool,
  CognitoUser
} from 'amazon-cognito-identity-js';
import Header from '../../components/Header/Header';
import { lock, mail } from 'ionicons/icons';
import Analytics from '../../components/SEO/Analytics';
import SlyHelmet from '../../components/SEO/Helmet';
import Footer from '../../components/Footer/Footer';

interface loginProps {
  title: string;
  route: string;
}
const Login: React.FunctionComponent<loginProps> = props => {
  // const submitLogin = () => {};

  const [member, setMember] = useState({
    email: '',
    password: ''
  });

  const [isFormInvalid, setIsFormInvalid] = useState(true);

  const emailRef: any = useRef(null);
  const passwordRef: any = useRef(null);

  const [formErrors, setFormErrors] = useState({ message: '' });
  const submitLogin = () => {
    const authenticationData = {
      Username: member.email,
      Password: member.password
    };
    const authenticationDetails = new AuthenticationDetails(authenticationData);

    const poolData = {
      UserPoolId: `${process.env.REACT_APP_USER_POOL_ID}`,
      ClientId: `${process.env.REACT_APP_CLIENT_ID}`
    };
    const userPool = new CognitoUserPool(poolData);
    const userData = {
      Username: member.email,
      Pool: userPool
    };
    const cognitoUser = new CognitoUser(userData);
    cognitoUser.authenticateUser(authenticationDetails, {
      onSuccess: (result: any) => {
        let accessToken = result.getAccessToken().getJwtToken();

        let idToken = result.idToken.jwtToken;
        // alert(JSON.stringify(idToken));
        window.open('#/account', '_self');
      },

      onFailure: (err: any) => {
        // alert(JSON.stringify(err));
        setFormErrors(err);
      }
    });
  };
  const getUser = () => {
    const data = {
      UserPoolId: `${process.env.REACT_APP_USER_POOL_ID}`,
      ClientId: `${process.env.REACT_APP_CLIENT_ID}`
    };
    var userPool = new CognitoUserPool(data);
    var cognitoUser = userPool.getCurrentUser();

    if (cognitoUser != null) {
      cognitoUser.getSession((err: any, session: any) => {
        if (err) {
          // alert(err);
          setFormErrors({ message: JSON.stringify(err) });
          return;
        }

        // console.log('session validity: ' + session.isValid());
        if (session.isValid() == true) window.open('#/account', '_self');
      });
    }
  };
  const canBeSubmitted = () => {
    emailRef.current != null &&
    passwordRef.current != null &&
    emailRef.current.value.length > 0 &&
    passwordRef.current.value.length >= 8
      ? setIsFormInvalid(false)
      : setIsFormInvalid(true);
    if (isFormInvalid == false)
      setMember({
        ...member,
        email: emailRef.current.value,
        password: passwordRef.current.value
      });
  };
  const submit = async () => {
    try {
      await submitLogin();
      setIsFormInvalid(true);
    } catch (e) {
      setFormErrors(e);
    }
  };

  getUser();

  return (
    <>
      <SlyHelmet {...props} />
      <Analytics pageName='login' />
      <Header pageTitle={'Login'} hideLogin={true} />
      <IonContent>
        <IonGrid>
          <IonRow className='ion-justify-content-center'></IonRow>
          <IonRow className='ion-justify-content-center'>
            <form
              style={{ width: '300px' }}
              onSubmit={e => {
                e.preventDefault();
                submit();
              }}
            >
              <IonRow className='ion-justify-content-center'>
                <IonText color='danger'>
                  <p>{formErrors ? formErrors.message : null}</p>
                </IonText>
              </IonRow>
              <IonList>
                <IonItem>
                  <IonLabel
                    onClick={() => emailRef.current.focus()}
                    position='floating'
                  >
                    Email
                  </IonLabel>
                  <IonInput
                    ref={emailRef}
                    name='email'
                    type='email'
                    // value={email}
                    debounce={100}
                    onIonChange={() => {
                      canBeSubmitted();
                    }}
                  >
                    <IonIcon
                      style={{ paddingLeft: '250px', position: 'absolute' }}
                      color='medium'
                      icon={mail}
                    />
                  </IonInput>
                </IonItem>
                <br />
                <IonItem>
                  <IonLabel
                    onClick={() => passwordRef.current.focus()}
                    position='floating'
                  >
                    Password
                  </IonLabel>
                  <IonInput
                    ref={passwordRef}
                    name='password'
                    type='password'
                    debounce={100}
                    onIonChange={() => {
                      canBeSubmitted();
                    }}
                  >
                    <IonIcon
                      style={{ paddingLeft: '250px', position: 'absolute' }}
                      color='medium'
                      icon={lock}
                    />
                  </IonInput>
                </IonItem>
              </IonList>
              {!isFormInvalid && <button hidden type='submit'></button>}
              <IonButton
                disabled={isFormInvalid}
                style={{ width: '100%' }}
                onClick={() => {
                  submit();
                }}
              >
                Log in
              </IonButton>
            </form>
          </IonRow>
          <br />
          <IonRow className='ion-justify-content-center'>
            <IonButton fill='outline' size='small' href='#/signup'>
              Sign UP
            </IonButton>
          </IonRow>
        </IonGrid>
      </IonContent>
      <Footer />
    </>
  );
};
export default Login;
