import { mail, lock, phonePortrait, person, people } from 'ionicons/icons';
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { CognitoUserPool } from 'amazon-cognito-identity-js';
import { IonContent, IonCard, IonCardHeader, IonRow } from '@ionic/react';
import './MyAccount.css';
import Header from '../../components/Header/Header';
import Footer from '../../components/Footer/Footer';
import Analytics from '../../components/SEO/Analytics';
import SlyHelmet from '../../components/SEO/Helmet';

interface myAccountProps {
  firstName?: string;
  lastName?: string;
  email?: string;
  phone?: string;
  role?: string;
  title: string;
  route: string;
}
const signOut = async () => {
  const data = {
    UserPoolId: `${process.env.REACT_APP_USER_POOL_ID}`,
    ClientId: `${process.env.REACT_APP_CLIENT_ID}`
  };
  let userPool = new CognitoUserPool(data);
  let cognitoUser = userPool.getCurrentUser();

  if (cognitoUser != null) {
    cognitoUser.signOut();
    window.open('#/login', '_self');
  }
};

const MyAccount: React.FunctionComponent<myAccountProps> = props => {
  const [member, setMember] = useState({
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    password: '',
    role: '',
    idToken: '',
    accessToken: '',
    sub: ''
  });

  const signUp = async (member: any) => {
    const axiosHeaders = {
      'Content-Type': 'application/x-www-form-urlencoded',
      'x-api-key': `${process.env.REACT_APP_API_PUB_KEY}`,
      Authorization: member.idToken
    };
    axios.request({
      method: 'POST',
      url: '/user',
      baseURL: process.env.REACT_APP_API_URL,
      //   data: '',
      headers: axiosHeaders,
      params: member
    });
  };

  const isLoggedIn = async () => {
    const data = {
      UserPoolId: `${process.env.REACT_APP_USER_POOL_ID}`,
      ClientId: `${process.env.REACT_APP_CLIENT_ID}`
    };
    let userPool = new CognitoUserPool(data);
    let cognitoUser = userPool.getCurrentUser();

    if (cognitoUser != null) {
      cognitoUser.getSession((err: any, session: any) => {
        if (err) {
          alert(err);
          return;
        }

        // console.log('session validity: ' + session.isValid());

        // console.log('session Access: ' + session.getAccessToken().getJwtToken());
        // console.log('session ID: ' + session.getIdToken().getJwtToken());
        // console.log('session Refresh: ' + session.getRefreshToken().getJwtToken()
        // );
        if (session.isValid() !== true) window.open('#/login', '_self');
        return true;
      });
    } else window.open('#/login', '_self');
    return false;
  };

  const getIdToken = async () => {
    const data = {
      UserPoolId: `${process.env.REACT_APP_USER_POOL_ID}`,
      ClientId: `${process.env.REACT_APP_CLIENT_ID}`
    };
    let userPool = new CognitoUserPool(data);
    let cognitoUser = userPool.getCurrentUser();

    if (cognitoUser != null) {
      cognitoUser.getSession((err: any, session: any) => {
        if (err) {
          alert(err);
          return;
        }
        let idToken = session.getIdToken().getJwtToken();
        let accessToken = session.getAccessToken().getJwtToken();

        setMember({
          ...member,
          idToken: idToken,
          accessToken: accessToken
        });
        getAttributes();
        // console.log(session.getIdToken().getJwtToken());
      });
    }
  };

  const getAttributes = async () => {
    const data = {
      UserPoolId: `${process.env.REACT_APP_USER_POOL_ID}`,
      ClientId: `${process.env.REACT_APP_CLIENT_ID}`
    };
    let userPool = new CognitoUserPool(data);
    let cognitoUser = userPool.getCurrentUser();

    if (cognitoUser != null) {
      cognitoUser.getSession((err: any, session: any) => {
        if (cognitoUser != null) {
          cognitoUser.getUserAttributes((err: any, result: any) => {
            if (err) {
              console.log(err);
              return;
            }
            const attributeMap: any = {};
            result.map((attribute: any, i: number) => {
              let attributeName = attribute.getName();
              switch (attributeName) {
                case 'sub':
                  attributeMap.sub = attribute.getValue();
                  break;
                case 'given_name':
                  attributeMap.firstName = attribute.getValue();
                  break;
                case 'family_name':
                  attributeMap.lastName = attribute.getValue();
                  break;
                case 'custom:role':
                  attributeMap.role = attribute.getValue();
                  break;
                default:
                  // do nothing;
                  break;
              }
              if (i == result.length - 1) {
                setMember({ ...member, ...attributeMap });
              }
            });

            // for (let i = 0; i < result.length; i++) {
            //   console.log(
            //     'attribute ' +
            //       result[i].getName() +
            //       ' has value ' +
            //       result[i].getValue()
            //   );
            // }
          });
        }
      });
    }
  };

  useEffect(() => {
    isLoggedIn();
    getIdToken();
    return () => {};
  }, []);

  return (
    <>
      <SlyHelmet {...props} />
      <Analytics pageName='account' />
      <Header
        pageTitle={`${member.firstName} ${member.lastName}`}
        hideLogin={true}
        hideSignUp={true}
        isLoggedIn={() => isLoggedIn()}
      />
      <IonContent>
        <IonRow className='ion-justify-content-center'>
          <IonCard>
            <IonCardHeader>Coming Soon!</IonCardHeader>
          </IonCard>
        </IonRow>
      </IonContent>
      <Footer />
    </>
  );
};

export default MyAccount;
export { signOut };
